/**
* 双随机-结果公示组件
* @module 系统
* @desc 查看
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" center top="50px"
                   width="80%">

            <table class="gridTable">
                <tr>
                    <td class="label">对象类型</td>
                    <td>
                        <span v-if="objArr.find(item=>{return item.dictValue == form.objectType})">{{objArr.find(item=>{return item.dictValue == form.objectType}).dictName}}</span>
                    </td>
                    <td class="label">对象名称</td>
                    <td>
                        <span>{{form.objectName}}</span>
                    </td>
                </tr>

                <tr>
                    <td class="label">检查单位</td>
                    <td>
                        <span>{{form.checkUnit |checkUnit}}</span>
                    </td>
                    <td class="label">检查人员</td>
                    <td>
                        <span>{{form.checkPerson}}</span>
                    </td>
                </tr>

                <tr>
                    <td class="label">检查内容</td>
                    <td colspan="4">
                        <span>{{form.checkValueName}}</span>
                    </td>
                </tr>
                <tr>
                    <td class="label">处罚方式</td>
                    <td>
                        <span>{{form.punishstr}}</span>
                    </td>
                    <td class="label">检查时间</td>
                    <td>
                        <span>{{form.checkTime|formatDate}}</span>
                    </td>
                </tr>
            </table>
        </el-dialog>
    </div>
</template>
<script>
    import {apiCheckPublicityGet} from "../../../api/case";
    import {apiCommonGetUsersByOrg} from "../../../api/system";
    import {formatDate, IFSEX} from "../../../until/wordBook";

    let that;
    export default {
        name: 'edit',
        props: {
            rowdata: {
                type: Object,
                default: {}
            },
            editId: {
                type: String,
                default: ''
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: 'normal'
            },
            type: {
                type: String,
                default: ''
            },
            options: {
                type: Array,
                default: []
            }
        },
        data() {

            return {
                cities: [{name: '警告', id: 'warning'}, {name: '教育劝阻', id: 'edu'}, {
                    name: '责令改正',
                    id: 'change'
                }, {name: '立案处罚', id: 'case'}],
                checkArr: [
                    {
                        value: 'month',
                        label: '每月检查'
                    },
                    {
                        value: 'quarter',
                        label: '每季度检查'
                    },
                    {
                        value: 'year',
                        label: '每年份检查'
                    }
                ],
                form: {},
                objArr: [],


            }
        },
        methods: {
            closeDialog() {
                this.$emit('update:show', false)
            },
            clearV() {
                this.$refs.ruleForm.clearValidate()
            },

            getUserListByOrg(data) {
                this.form.orgName = this.options.find(i => {
                    return i.orgId === data
                }).orgName;
                apiCommonGetUsersByOrg(data).then(res => {
                    this.userList = res.userList
                })
            },

            //弹框弹出时获取data给form
            initPage() {
                //检查对象类型下拉
                this.$until.workBookApi('checkObjectType').then(res => {
                    this.objArr = res;
                });

                if (this.editId) {
                    //    查看数据
                    apiCheckPublicityGet(this.editId).then(res => {
                        this.form = res.data;
                        let punishArr = (this.form.punish.split(',')).filter(item => item);
                        let namestr = '';
                        this.cities.forEach(item => {
                            punishArr.forEach(item2 => {
                                if (item.id == item2) {
                                    namestr += item.name + ' ,'
                                }
                            })
                        })
                        this.form.punishstr = namestr.substr(0, namestr.length - 1)
                    })
                } else {

                }
            }

        },
        beforeCreate() {
            that = this
        },
        filters: {
            ifsex(val) {
                return IFSEX[val]
            },
            formatDate(value) {
                return formatDate(value, 'yyyy-MM-dd')
            },
            checkUnit(val) {
                if (that.options.find(item => {
                    return item.orgId == val
                })) {
                    return that.options.find(item => {
                        return item.orgId == val
                    }).orgName
                }
            }

        },
        //监听，如果show为true,则执行initPage()
        watch: {
            'show'(value) {
                if (value) {
                    this.initPage();

                }
            }
        },

    }
</script>
<style lang='less' scoped>

    table {
        width: 100%;
        border: 1px solid #ccc;

        tr {
            height: 50px;

            td {
                padding-left: 5px;
                border: 1px solid #ccc;
            }
        }
    }

    .alert_wrop {
        padding: 10px;
        display: flex;
        justify-content: space-between;

    }

    .widthselect {
        width: 100%
    }

    .cssfont {
        font-weight: bold;
    }
</style>


